let lastUpdateTime;

const currentLocation = (setUserLocation, setErrorMessage) => {
    if (window.navigator.geolocation !== undefined) {

        const success = (position) => {
            const location = {
                lat: position.coords.latitude ? position.coords.latitude : undefined,
                lng: position.coords.longitude ? position.coords.longitude : undefined,
            }

            if (process.env.NODE_ENV === "development") {
                console.log("getCurrentPosition:");
                console.log(location);
            }

            setUserLocation(location);
        }

        const error = (error) => {

            const errorTypes = {
                0: "不明原因錯誤",
                1: "使用者拒絕提供位置資訊",
                2: "無法取得位置資訊",
                3: "位置查詢逾時"
            };

            setErrorMessage(errorTypes[error.code])
        }

        const optoptions = {
            enableHighAccuracy: true,
            timeout: 5000,
            maximumAge: 0
        };

        window.navigator.geolocation.getCurrentPosition(success, error, optoptions);
    }
}

const watchCurrentLocation = (setUserLocation, setErrorMessage) => {
    const success = (position) => {
        const location = {
            lat: position.coords.latitude ? position.coords.latitude : undefined,
            lng: position.coords.longitude ? position.coords.longitude : undefined,
        }

        const now = new Date();
        var duration = Math.abs(now - lastUpdateTime) / 1000;
        const durationSeconds = duration % 60;
        const updateSecondLimitation = 180; //3 分鐘通知一次

        let allowUpdate = durationSeconds > updateSecondLimitation ? true : false;

        if (allowUpdate) {
            setUserLocation(location);
            lastUpdateTime = new Date();
        }

        if (process.env.NODE_ENV === "development") {
            console.log("watchPosition:");
            console.log(location);
        }
    }

    const error = (error) => {

        const errorTypes = {
            0: "不明原因錯誤",
            1: "使用者拒絕提供位置資訊",
            2: "無法取得位置資訊",
            3: "位置查詢逾時"
        };

        setErrorMessage(errorTypes[error.code])
    }

    const options = {
        enableHighAccuracy: true,
        timeout: 5000,
        maximumAge: 0
    };

    lastUpdateTime = lastUpdateTime ? lastUpdateTime : new Date();

    if (window.navigator.geolocation !== undefined) {
        const id = navigator.geolocation.watchPosition(success, error, options);
        return id;
    }
}

const clearWatchPosition = (id) => {
    if (window.navigator.geolocation !== undefined) {
        navigator.geolocation.clearWatch(id);
    }
}

export { currentLocation, watchCurrentLocation, clearWatchPosition, lastUpdateTime };